.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Big-Lukas {
  position: absolute;
  display: inline-block;
  font-size: 15vw;
  height: 30vh;
  width: 100%;
  bottom: 40%;
  text-align: left;
}

.Big-Viskanta {
  position: absolute;
  display: inline-block;
  font-size: 15vw;
  height: 30vh;
  width: 100%;
  top: 40%;
  text-align: right;
}

.Thanks-For {
  position: absolute;
  display: inline-block;
  width: 20%;
  top: 40%;
  animation: spin 8s infinite linear;

}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.Page {
  background-color: ;
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 0px;
  margin: 0px;
  font-size: 100%;
}

.Logo {
  position: absolute;
  width: 8%;
  display: inline-block;
  top: 0.75vh;
  right: 10px;
}

.Nav-Text {
  display: inline-block;
  position: absolute;
  top: -1vh;
  left: 6vw;
  color: white;
}

.NavBar {
  position: fixed;

  width: 100%;
  height: 7%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
